.board {
  @apply grid grid-cols-3 gap-1 rounded-sm bg-zinc-200 border-4 transition-all duration-500 my-5 relative shadow-2xl;
}

.turnX {
  @apply bg-red-500 border-4 border-red-500;
}

.turnO {
  @apply bg-blue-500 border-4 border-blue-500;
}

.info {
  @apply grid grid-cols-1 absolute z-10 -top-1 -left-1;
}

.win {
  @apply bg-green-800 border-green-800;
}

.draw {
  @apply bg-yellow-500 border-yellow-500;
}
